@import '../styles/variables';
@import '../styles/mixins';
@import '../styles/utility';
@import '../styles/buttons';

#secondaryOnboardingContainer {
  width: 100%;
  height: 240px;
  box-shadow: inset 0 -1px 4px 0 rgba(0, 0, 0, 0.15);
  background-color: #e4eaee;

  #collapseToggleElement {
    position: relative;
    float: right;
    padding-right: 65px;
    top: 84px;
    text-align: right;
    cursor: pointer;
    z-index: 1;

    #collapseToggleArrow {
      padding-left: 8px;
      padding-bottom: 2px;
    }
  }
}

.empty .secondaryOnboarding {
  top: 280px;
}

.secondaryOnboarding {
  .middlePosition {
    top: 235px;
  }
  #tools, #ad {
    &.middlePosition {
      top: 60px;
    }
  }

  .bottom {
    top: 295px;
  }
  .emptyVault {
    top: -155px;
    overflow: hidden;
  }

  #emptySites,
  #emptyNotes,
  #emptyFormFills,
  #emptySentShares,
  #emptySharedFolders,
  #emptyReceivedShares,
  #emptyEmergencyTrusted,
  #emptyEmergencyTrusting,
  #emptyCreditMonitoring
  {
    .emptyVault {
      top: 0;

      .tour {
        margin-top: 0;
        top: 0;
      }
    }
  }

  &.collapsed {

    #secondaryOnboardingContainer {
      width: 100%;
      height: 120px;
      box-shadow: inset 0 -1px 4px 0 rgba(0, 0, 0, 0.15);
      background-color: #e4eaee;
    }

    #main {
      top: 60px;
    }

    #mainScroll {
      top: 115px;
    }

    #options {
      top: 115px;
    }
  }
}